:root {
    --poker-table-width: 80vw;
    --poker-table-height: 25vw;

    --blur-amount: 10px;

    --card-height: 4.5vw;
    --card-width: 3.2vw;
    --card-font-size: 1.7vw;

    --default-padding: 10px;
}

.poker-table-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /*height: 100%;*/
    /*width: 100%;*/
    /*min-height: 90vh;*/
    min-width: 100%;
    max-height: 90vh;
}

.poker-table {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(3, 1fr);
    gap: 10px;
    padding: 10px;
    /*flex*/
    justify-content: center;
    align-items: center;
    border-radius: calc(var(--poker-table-height) / 2);
    width: 90vw;
    height: 90vh;
    min-height: 100vh;
    position: relative;
    margin-left: auto; /* Center the table on the page */
    margin-right: auto; /* Center the table on the page */
    backdrop-filter: blur(var(--blur-amount));
    background-color: rgba(0, 0, 0, 0.1);
    border: 1px solid black;
    /*margin-top: 200px;*/
}

.community-cards {
    display: flex;
    /*position: absolute;*/
    justify-content: center;
    align-items: center;
    grid-column: 3 / span 2; /* Span the middle two columns */
    grid-row: 2; /* Middle row */
    justify-self: center;
    align-self: center;
}

.table-pot-420 {
    color: white;
    font-weight: bold;
    display: flex;
    height: var(--card-height);
    width: var(--card-height);
    border-radius: calc(var(--card-height) / 2);
    justify-content: center;
    align-items: center;
    position: relative;
}

.card {
    width: var(--card-width); /* Adjust the size as needed */
    height: var(--card-height); /* Adjust the size as needed */
    margin: 0.5vh;
    background-color: #ffffff; /* TCard background */
    display: flex;
    flex-direction: column; /* Stack children vertically */
    justify-content: space-between; /* Distributes children evenly */
    align-items: center;
    border-radius: calc(var(--card-width) / 8);
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.2);
    font-family: 'Futura', sans-serif;
    position: relative;
    border: 1px solid black; /* TCard border */
    overflow: hidden; /* Ensures content does not overflow */
    padding: 3px 0; /* Vertical padding at the very top and bottom */
}

.card.hidden {
    padding: 0;
}

.card-value,
.card-suit {
    width: 100%; /* Full width */
    text-align: center;
    overflow: hidden; /* Ensures content does not overflow */
    white-space: nowrap; /* Prevents wrapping */
}

.card-value {
    height: 50%; /* Half the height of the card */
    font-size: var(--card-font-size); /* Adjust as needed */
    display: flex;
    justify-content: center;
    align-items: flex-end; /* Align to bottom of the top half */
}

.card-suit {
    height: 50%; /* Half the height of the card */
    font-size: var(--card-font-size); /* Adjust as needed */
    color: red; /* Default color, it changes for different suits */
    display: flex;
    justify-content: center;
    align-items: flex-start; /* Align to top of the bottom half */
}

.hearts,
.diamonds {
    color: red;
}

.clubs,
.spades {
    color: black;
}

.card-back {
    width: 100%;
    height: 100%;
    background-color: #1a1a1a; /* Dark color for card back */
    /*border-radius: inherit;*/
    /* Add more styling for the card back if needed */
}

