.player-container {
    display: flex;
    flex-direction: column;
    width: fit-content;
    justify-content: center;
    align-items: center;
    position: relative;
    /*width: calc(4 * var(--card-width));*/
}

.player-container.winner {
    border: 4px solid var(--color-green-light);
    border-radius: calc(var(--card-width) / 2);
    padding: var(--default-padding);
    backdrop-filter: blur(10px);
    background-color: var(--color-dark-grey);
    z-index: 100;
}

.player-container.thinking {
    border: 2px solid var(--color-yellow-light);
    border-radius: calc(var(--card-width) / 2);
    padding: var(--default-padding);
    backdrop-filter: blur(10px);
    background-color: var(--color-dark-grey);
    z-index: 100;
}

.player-container.human-player {
    border: 2px solid white;
    border-radius: calc(var(--card-width) / 2);
    padding: var(--default-padding);
    backdrop-filter: blur(10px);
    background-color: var(--color-dark-grey);
    z-index: 100;
}

.player {
    /*position: absolute;*/
    width: fit-content;
    min-width: calc(var(--card-width) * 3);
    /*height: calc(3 * var(--card-height));*/
    border-radius: calc(var(--card-width) / 2);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    line-height: 30px; /* Vertically center the text */
    border: 1px solid #000;
    /*border-radius: 15px; !* Half of the player element height for rounded corners *!*/
    background-color: white;
    padding: var(--default-padding);
    color: black;
}

.player-folded {
    background-color: var(--color-dark-grey);
    color: white;
}

.folded-text {
    font-style: italic;
}

.player-cards {
    display: flex;
    flex-direction: row;
    margin-top: 10px;
}

.position-and-winnings-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;
}

.player-winnings {
    display: flex;
    flex-direction: row;
    margin-bottom: 10px;
    color: var(--color-green);
}

.player-position {
    display: flex;
    color: white;
    font-weight: bold;
    width: 30px;
    height: 30px;
    border-radius: 15px;
    border: 3px solid white;
    align-items: center;
    justify-content: center;
}

.player-position.dealer {
    background-color: white;
    color: var(--color-dark-grey);
}

.player-stack-and-action-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
    margin-bottom: 10px;
}

.player-action {
    width: fit-content;
    font-weight: bold;
    color: white;
}

.all_in {
    color: var(--color-red);
}

.check {
    color: white;
}

.call {
    color: var(--color-yellow);
}

.bet,
.raise {
    color: var(--color-green);
}

.player-stack {
    width: fit-content;
    color: white;
}

.player-info-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.player-image {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: white;
    border: 1px solid black;
    margin-bottom: 10px;
    margin-top: 10px;
}

.player-actions-container {
    display: flex;
    flex-direction: column;
    justify-content: right;
    align-items: center;
    margin-top: 10px;
}

.player-actions-bet-container {
    display: flex;
    flex-direction: row;
    align-content: space-between;
    justify-content: space-between;
    align-items: center;
    padding: var(--default-padding);
}

.action-button {
    /*margin-left: auto;*/
    height: fit-content;
    padding: 5px;
    background-color: var(--color-green);
    border-radius: 5px;
    cursor: pointer;
    font-size: calc(var(--font-size) * 0.85);
}

.fold-button {
    margin-top: 10px;
    background-color: var(--color-red);
}

.check-button {
    margin-top: 10px;
    background-color: var(--color-yellow);
}

.slider-container {
    position: relative;
    /*width: calc(var(--card-width) * 2);*/
    margin-top: 20px;
    margin-right: 10px;
}

.slider {
    /*width: 100%;*/
}

.slider-text {
    position: absolute;
    bottom: 100%;
    transform: translateX(-50%);
    white-space: nowrap;
    line-height: var(--font-size);
    background-color: white;
    border-radius: 5px;
}
